.compact-table th,
.compact-table td {
  border: 1px solid #ddd;
  padding: 6px 8px;
  text-align: center;  /* ✅ All text centered */
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Column widths */
.compact-table th:nth-child(1),
.compact-table td:nth-child(1) { width: 15%; } /* Category */
.compact-table th:nth-child(2),
.compact-table td:nth-child(2) { width: 10%; } /* Item */
.compact-table th:nth-child(3),
.compact-table td:nth-child(3) { width: 25%; } /* Sub Item */
.compact-table th:nth-child(4),
.compact-table td:nth-child(4),
.compact-table th:nth-child(5),
.compact-table td:nth-child(5) { width: 10%; } /* Prices */
.compact-table th:nth-child(6),
.compact-table td:nth-child(6) { width: 15%; } /* Actions */

