/* General Layout */
.admin-page {
  padding: 20px;
  font-family: Arial, sans-serif;
  background: #f9f9f9;
  min-height: 100vh;
}

h2 {
  margin-bottom: 20px;
  color: #222;
}

h3 {
  margin: 15px 0;
  color: #333;
}

/* Tabs */
.tabs {
  margin-bottom: 20px;
}

.tabs button {
  margin-right: 10px;
  padding: 8px 14px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background: #e0e0e0;
  font-weight: bold;
}

.tabs button.active {
  background: #007bff;
  color: white;
}

/* Filters */
.filters {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  gap: 10px;
}

.filters select,
.filters input {
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  min-width: 150px;
}

.filters .add-btn {
  background: #28a745;
  color: white;
  padding: 6px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.filters .add-btn:hover {
  background: #218838;
}

/* Table Styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
  background: white;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

table thead {
  background: #007bff;
  color: white;
}

table th,
table td {
  border: 1px solid #ddd;
  padding: 6px 8px;
  text-align: center;   /* ✅ all text centered */
  font-size: 14px;
  white-space: nowrap;
}

table tr:nth-child(even) {
  background: #f2f2f2;
}

table tr:hover {
  background: #f9f9f9;
}

/* Compact table specifically */
.compact-table th,
.compact-table td {
  padding: 4px 6px; /* tighter rows */
  font-size: 13px;
}

/* Buttons */
button {
  margin: 0 2px;
  padding: 4px 8px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
}

button:hover {
  opacity: 0.9;
}

button.add-btn {
  background: #28a745;
  color: #fff;
}

button.save-btn {
  background: #007bff;
  color: #fff;
}

button.cancel-btn {
  background: #6c757d;
  color: #fff;
}

button.delete-btn {
  background: #dc3545;
  color: #fff;
}

button.edit-btn {
  background: #17a2b8;
  color: #fff;
}
