* { box-sizing: border-box; }
body { margin: 0; font-family: system-ui, -apple-system, Segoe UI, Roboto, Arial; background: #fafafa; color: #222; }
h2, h3, h4 { margin: 12px 0; }
button { padding: 8px 12px; border: 1px solid #ddd; border-radius: 8px; cursor: pointer; background:#fff; }
button + button { margin-left: 8px; }
table { width: 100%; border-collapse: collapse; background: #fff; }
th, td { border: 1px solid #eee; padding: 8px; text-align: left; }
th { background: #f6f6f6; }
input { padding: 8px; border: 1px solid #ddd; border-radius: 8px; }
.product-tile, .cart, .catalog, .admin-page { background: #fff; border: 1px solid #eee; border-radius: 12px; padding: 12px; margin-bottom: 12px; }
.tabs { display: flex; gap: 8px; margin: 12px 0; }
.tabs .active { background: #111; color: #fff; border-color: #111; }
